body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 0px;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(180deg,#2f6ed3 5%,#5095e4 20%,#5095e4 60%,#2f6ed3 95%);
    background-repeat: no-repeat;
    background-size: cover;
}

#root
{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Juegos
{
    width: 80%;
    height: 80vh;
    background: rgba( 225, 218, 218, 0.5 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15.5px );
    -webkit-backdrop-filter: blur( 15.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.SelectorJuegos
{
    display: flex;
    justify-content: center;

}

.NombreJuegos button
{
    background: rgba( 225, 218, 218, 0.5 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15.5px );
    -webkit-backdrop-filter: blur( 15.5px );
    text-decoration: none;
    border: none;
    border-radius: 0%;
    border-bottom: 1px solid black;
    font-size: large;
}

.NombreJuegos button:focus
{
    background: rgba(179, 172, 172, 0.5);
    box-shadow: 0 8px 32px 0 rgba(19, 24, 95, 0.37);
    backdrop-filter: blur( 15.5px );
    -webkit-backdrop-filter: blur( 15.5px );
    backface-visibility: 0;
    text-decoration: none;
    border: none;
    border-radius: 0%;
    border-bottom: 1px solid black;
    font-size: large;
}

.JuegoTres
{
    width: 100%;
    height: 75vh;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.TresEnRaya
{
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}
