body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}


.Ahorcado
{
    margin-top: 50px;
}

.game-aho
{
    display: flex;
    flex-wrap: wrap;
}

.game-board-aho
{
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-content: space-between;
    align-items: center;
}

.palabra
{
    display: flex;
    text-align: center;
}

.linea
{
    width: 20px;
    border-bottom: 1px solid black;
    margin-left: 5px;
}